// @flow
import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { multiClass } from 'utils/utils';
import * as styles from './AuthForm.scss';

const validate = (values: AuthFormValues) => {
  const errors = {};

  if (!values.login) {
    errors.login = 'Enter your login';
  }

  if (!values.password) {
    errors.password = 'Enter your password';
  }

  return errors;
};

const RenderInputField = ({ input, type, placeholder, meta: { touched, error } }) => (
  <div className={styles.authFormField}>
    <input
      {...input}
      className={multiClass(
        styles.authFormFieldInput,
        { clsName: styles.authFormFieldInputError, isActive: touched && error },
      )}
      placeholder={placeholder}
      type={type}
    />
    {touched && error && <span className={styles.authFormFieldErrorMsg}>{error}</span>}
  </div>
);

const AuthForm = ({ handleSubmit, submitting }) => (
  <form
    className={styles.authForm}
    onSubmit={handleSubmit}
  >
    <Field
      name="login"
      type="text"
      placeholder="Login..."
      component={RenderInputField}
    />
    <Field
      name="password"
      type="password"
      placeholder="Password..."
      component={RenderInputField}
    />
    <button
      type="submit"
      className={styles.authFormSingInBtn}
      disabled={submitting}
    >
      Sing in
    </button>
  </form>
);

export default reduxForm({
  form: 'authentication',
  validate,
})(AuthForm);



