// @flow
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { login } from 'actions/authActions';

import AuthForm from 'components/AuthForm';
import Footer from 'components/Footer';

import * as styles from './AuthPage.scss';

class AuthPage extends Component<AuthPageProps, AuthPageState> {
  state = {
    users: JSON.parse(localStorage.getItem('users') || ''),
  };

  handleAuthSubmit = (values: authFormValues): void => {
    this.props.authHandler(values.login, values.password);
  };

  render() {
    return (
      <div className={styles.authPage}>
        <div className={styles.authPageAvailableUsers}>
          <div className={styles.authPageAvailableUsersTitle}>Available users list</div>
          <div>
            {this.state.users && this.state.users.map(user => (
              <div
                key={user.login}
                className={styles.authPageAvailableUsersItem}
              >
                {`{ login: ${user.login}, password: ${user.password} }`}
              </div>
            ))}
          </div>
        </div>
        <div className={styles.authPageInnerWrapper}>
          <AuthForm onSubmit={this.handleAuthSubmit} />
        </div>
        <Footer />
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch: Function) => ({
  authHandler: (uLogin, uPassword) => dispatch(login(uLogin, uPassword)),
});

export default connect(undefined, mapDispatchToProps)(AuthPage);



